@use "base/variables" as v;
@use "base/mixins" as m;

.proyectos {
  background-color: v.$gris-oscuro;
  color: v.$blanco;
  text-align: center;
  padding: 2rem;
  h2{
    border-bottom: 0.5rem solid v.$azul-neon;
    display: inline-block;
  }
}
.contenido-proyectos {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

.mySlides {
  display: none;

  img {
    width: 100%;
    height: 50rem;
    object-fit: cover;
  }
}
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 100%;
  width: auto;
  margin-top: -22px;
  padding: 1.6rem;
  color: v.$azul-neon;
  font-weight: bold;
  font-size: 1.8rem;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;

  &:hover {
    background-color: rgba(0, 0, 0, 0.8);
  }
}

.next {
  right: 0;
  left: 100%;
  border-radius: 3px 0 0 3px;
}

.text {
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #ffffff;
  padding: 8px 12px;
  position: absolute;
  bottom: -4rem;
  width: 100%;
  text-align: center;
  input{
    cursor: pointer;
    background-color: v.$azul-neon;
  }
}

.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 5rem 0 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active,
.dot:hover {
  background-color: v.$azul-neon;
}

/* Fading animation */
.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
