@use "base/variables" as v;
@use "base/mixins" as m;

.video {
  position: relative;
  max-height: 70rem;
  min-width: 50rem;
  overflow: hidden;

  .overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: rgba(24, 24, 24, 0.842);
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  video {
    width: auto;
    display: block;
    @include m.tablet{
       width: 100%;
    }
  }

}

.contenido-video {
    z-index: 1;
    p{
      background-color: v.$azul-neon;
      padding: 1rem;
      display: inline-block;
      margin: 0;
    }
  h2 {
      color: v.$blanco;
      font-size: 3rem;
      margin: 0;
      @include m.tablet {
        font-size: 5rem;
        margin: 0;
        p{
          font-size: 2rem;
          margin: 0;
        }
      }
  }

}