@use "base/variables" as v;
@use "base/mixins" as m;

.sobre-mi {
  background-color: v.$gris-oscuro;
  padding: 5rem;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  column-gap: 2rem;
  @include m.tablet {
    display: grid;
    height: 52rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.imagen {
  margin: 0 auto;
  img {
    height: 40rem;
    object-fit: cover;
    -webkit-box-shadow: -20px 22px 36px 0px #22ffff;
    box-shadow: -26px 21px 27px 0px #22ffff;
  }
}

.contenido-sobre-mi {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .enlaces-sobre-mi {
    display: flex;
    column-gap: 2rem;
    list-style: none;
    font-size: 3rem;
    padding: 0;

    @include m.tablet {
      column-gap: 12rem;
    }
  }
  a {
    color: v.$blanco;
    padding: 1rem;
    border-bottom: 0.4rem solid v.$azul-neon;
    &:hover {
      background-color: v.$azul-neon;
      color: black;
      transition: 0.5s;
    }
  }

  h2 {
    color: v.$blanco;
    font-size: 4rem;
    border-bottom: 0.5rem solid v.$azul-neon;
  }
  p {
    color: v.$blanco;
    font-size: 2rem;
    margin: 0;
  }
}
