@use "base/variables" as v;
@use "base/mixins" as m;

.header {
  background-color: v.$gris-oscuro;
  h1 {
    color: v.$blanco;
    text-align: center;
    @include m.tablet {
      margin: 0;
    }
  }
  .contenido-header {
    padding: 2rem;
    text-align: center;
    @include m.tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}

.navegacion-principal {
  @include m.tablet {
    display: flex;
    gap: 2rem;
  }
  a {
    display: block;
    color: v.$blanco;
    margin-bottom: 1rem;

    @include m.tablet {
      margin: 0;
    }
    &:hover {
      color: v.$azul-neon;
    }
  }
}
