@use 'base/variables' as v;
@use 'base/mixins' as m;

.skill {
    background-color: v.$gris-claro;
    padding: 3rem 0 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2 {
        color: v.$blanco;
        text-align: center;
        font-size: 4rem;
        border-bottom:0.5rem solid v.$azul-neon;
        display: inline-block;
        
    }
}

.ul-skill {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(3.1fr);
    column-gap: 2rem;
    color: v.$blanco;
    text-align: center;
    list-style: none;
    justify-items: center;
    margin: 0;
    padding: 0;
   
    @include m.desktop {
        grid-template-columns: repeat(7,1fr);
    }
    
    li {
        width: 15rem;
    }
}