@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    background-color: v.$gris-claro;
    color: v.$blanco;
}

.contenido-footer{
    display: flex;
    justify-content: space-around;
    a{
        width: 6rem;

        &:hover{
            -webkit-transform: scale(1.25);
        }
    }
}