html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Akshar", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 90%;
  max-width: 180rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  display: block;
}

.header {
  background-color: #121212;
}
.header h1 {
  color: white;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  color: white;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #22ffff;
}

.video {
  position: relative;
  max-height: 70rem;
  min-width: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(24, 24, 24, 0.842);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
}
.contenido-video p {
  background-color: #22ffff;
  padding: 1rem;
  display: inline-block;
  margin: 0;
}
.contenido-video h2 {
  color: white;
  font-size: 3rem;
  margin: 0;
}
@media (min-width: 768px) {
  .contenido-video h2 {
    font-size: 5rem;
    margin: 0;
  }
  .contenido-video h2 p {
    font-size: 2rem;
    margin: 0;
  }
}

.sobre-mi {
  background-color: #121212;
  padding: 5rem;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  column-gap: 2rem;
}
@media (min-width: 768px) {
  .sobre-mi {
    display: grid;
    height: 52rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.imagen {
  margin: 0 auto;
}
.imagen img {
  height: 40rem;
  object-fit: cover;
  -webkit-box-shadow: -20px 22px 36px 0px #22ffff;
  box-shadow: -26px 21px 27px 0px #22ffff;
}

.contenido-sobre-mi {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contenido-sobre-mi .enlaces-sobre-mi {
  display: flex;
  column-gap: 2rem;
  list-style: none;
  font-size: 3rem;
  padding: 0;
}
@media (min-width: 768px) {
  .contenido-sobre-mi .enlaces-sobre-mi {
    column-gap: 12rem;
  }
}
.contenido-sobre-mi a {
  color: white;
  padding: 1rem;
  border-bottom: 0.4rem solid #22ffff;
}
.contenido-sobre-mi a:hover {
  background-color: #22ffff;
  color: black;
  transition: 0.5s;
}
.contenido-sobre-mi h2 {
  color: white;
  font-size: 4rem;
  border-bottom: 0.5rem solid #22ffff;
}
.contenido-sobre-mi p {
  color: white;
  font-size: 2rem;
  margin: 0;
}

.skill {
  background-color: #161616;
  padding: 3rem 0 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.skill h2 {
  color: white;
  text-align: center;
  font-size: 4rem;
  border-bottom: 0.5rem solid #22ffff;
  display: inline-block;
}

.ul-skill {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3.1fr);
  column-gap: 2rem;
  color: white;
  text-align: center;
  list-style: none;
  justify-items: center;
  margin: 0;
  padding: 0;
}
@media (min-width: 992px) {
  .ul-skill {
    grid-template-columns: repeat(7, 1fr);
  }
}
.ul-skill li {
  width: 15rem;
}

.proyectos {
  background-color: #121212;
  color: white;
  text-align: center;
  padding: 2rem;
}
.proyectos h2 {
  border-bottom: 0.5rem solid #22ffff;
  display: inline-block;
}

.contenido-proyectos {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

.mySlides {
  display: none;
}
.mySlides img {
  width: 100%;
  height: 50rem;
  object-fit: cover;
}

.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 100%;
  width: auto;
  margin-top: -22px;
  padding: 1.6rem;
  color: #22ffff;
  font-weight: bold;
  font-size: 1.8rem;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}
.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.next {
  right: 0;
  left: 100%;
  border-radius: 3px 0 0 3px;
}

.text {
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #ffffff;
  padding: 8px 12px;
  position: absolute;
  bottom: -4rem;
  width: 100%;
  text-align: center;
}
.text input {
  cursor: pointer;
  background-color: #22ffff;
}

.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

.dot {
  cursor: pointer;
  height: 15px;
  width: 15px;
  margin: 5rem 0 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active,
.dot:hover {
  background-color: #22ffff;
}

/* Fading animation */
.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
.footer {
  background-color: #161616;
  color: white;
}

.contenido-footer {
  display: flex;
  justify-content: space-around;
}
.contenido-footer a {
  width: 6rem;
}
.contenido-footer a:hover {
  -webkit-transform: scale(1.25);
}